import React, { Component } from 'react'
import "./style/style.scss"
import { DatePicker, Form, Input, Pagination } from 'antd';
import data from "@/mock/college/index";
import LazyImage from "../../../components/lazyImage/index"
import Nav from "@/components/topNav/"
const { RangePicker } = DatePicker;
const { Search } = Input;
export default class index extends Component {
    state = {
        brr: ["全部", "分类一", "分类二", "分类三", "分类四"],
        ind: 0,
        arr: ["今日", "本周", "本月"],
        currentIndex: 0
    }
    render() {
        let { brr, ind, arr, currentIndex } = this.state;
        return (
            <div className="wrap">
                <Nav navArr={brr} switchFn={this.switchFn} />
                {(ind === 1 || ind === 0) && <div>
                    <div className="center">
                        <div className="center-left">
                            {
                                arr.map((ite, index) => <span key={index}>{ite}</span>)
                            }
                            <Form>
                                <Form.Item>
                                    <RangePicker
                                        dateRender={current => {
                                            const style = {};
                                            if (current.date() === 1) {
                                                style.border = '1px solid #1890ff';
                                                style.borderRadius = '50%';
                                            }
                                            return (
                                                <div className="ant-calendar-date" style={style}>
                                                    {current.date()}
                                                </div>
                                            );
                                        }}
                                    />
                                </Form.Item>
                            </Form>
                        </div>
                        <div className="center-right">
                            <div className="input">
                                <Form className="form">
                                    <Form-Item>
                                        <Search placeholder="input search text" style={{ width: 200 }} />
                                    </Form-Item>
                                </Form>
                            </div>
                        </div>
                    </div>
                    <div className="wrap-title">
                        <div className="title-top">
                            {
                                data.map((item, index) =>
                                    <span key={index} className={index === currentIndex ? 'active' : ''} onClick={() => {
                                        this.setState({
                                            currentIndex: index
                                        })
                                    }}>{item.title}</span>)
                            }
                        </div>
                        <div className="title-bottom">
                            {
                                data[currentIndex].children.map((item, index) => <dl key={index}>
                                    <dt>
                                        <LazyImage data_src={require(`../../../static/img/${item.img}`)} />
                                    </dt>
                                    <dd>
                                        <p>{item.title}</p>
                                        <p>
                                            <span>{item.name}</span>
                                            <span>{item.date}</span>
                                        </p>
                                    </dd>
                                </dl>)
                            }
                        </div>
                        <Pagination showQuickJumper defaultCurrent={2} total={500} />
                    </div>
                </div>
                }
            </div>)
    }
    switchFn = (index) => {
        this.setState({
            ind: index
        })
    }
}
