import React, { Component } from 'react'

class LazyImage extends Component {
    state = {
        src: require("../../static/img/timg.gif")
    }
    render() {
        let { src } = this.state
        return (
            <>
                <img src={src} />
            </>
        )
    }
    componentDidMount() {
        let { data_src } = this.props
        setTimeout(() => {
            this.setState({
                src: data_src
            })
        }, 1500)
    }
}

export default LazyImage;